.card {
  background-color: #FFFFFF;
  border-radius: 5px;
  box-shadow: 0px 0px 1px 0px rgba(51, 51, 51, 0.6);
  color: #333333;
  margin: 0 auto;
  max-width: 320px;
  text-align: left;
  padding: 10px;
  width: 95%; }
  .card .featured {
    margin-top: 20px;
    text-align: center; }
  .card__title {
    font-family: "Open Sans", sans-serif;
    font-size: 30px;
    font-weight: normal;
    margin: 0;
    text-shadow: 1px 1px 2px rgba(150, 150, 150, 0.6); }
  .card__body {
    background-color: #E9E9E9;
    border-radius: 5px;
    box-shadow: inset 0px 0px 2px 0px rgba(51, 51, 51, 0.65);
    padding: 15px 10px; }
  .card__label {
    background-position: left center;
    background-repeat: no-repeat;
    color: #666666;
    font-size: 12px;
    padding-left: 18px; }
    .card__label.population {
      background-image: url(../../assets/images/ewok.png); }
    .card__label.climate {
      background-image: url(../../assets/images/climate.png); }
    .card__label.terrain {
      background-image: url(../../assets/images/terrain.png); }
  .card__info {
    font-size: 14px; }

.info-wrapper {
  margin-bottom: 10px; }



